import dwSelectCenterDirective from 'shared/dw-controls/dw-select-center/dw-select-center.directive';
import optsPaperSize from 'shared/reports/services/opts-paper-size.service';
import template from './report-options.tpl.html';

/**
 * reportOptions directive - wraps report options: center, paper size, paper layout
 */
export default angular.module('deskworks.reportOptions', [
  'deskworks.helper',
  optsPaperSize.name,
  dwSelectCenterDirective.name,
])

  .directive('reportOptions', function($compile) {

    return {
      restrict: 'EAC',
      scope: true,
      transclude: 'element',
      priority: 2, // To be higher than ngModel directive with priority: 1
      controller: 'ReportOptionsCtrl as $ctrl',
      bindToController: {
        model: '<',
        centers: '<',
        allCenters: '<',
        banks: '<',
        noCenters: '<',
      },

      link: ($scope, $element, $attrs, $ctrl, $transclude) => {
        $transclude((clone, newScope) => {
          const tpl = $compile(template)($scope);
          angular.element(tpl[0].querySelector ? tpl[0].querySelector('.ff-panel-body') : tpl).append(clone); // append custom content before compilation
          $element.after(tpl);
        });
      },
    };
  })

  .controller('ReportOptionsCtrl', function($scope, $location, state, helper, optsPaperSize) {
    const $ctrl = this;

    $ctrl.optsPaperSize = optsPaperSize;

    /**
     * $onInit
     */
    $ctrl.$onInit = () => {
      if ($ctrl.model.centerId !== false && !$ctrl.noCenters) watchCenterId();
      if ($ctrl.banks) watchBankId();
      watchPaperSize();
      watchPaperLayout();
    };

    /**
     * Map centerId to URL search param
     */
    function watchCenterId() {
      $scope.$watch(() => $location.search().centerId, val => {
        $ctrl.model.centerId = val || val === 0 ? +val : state.getCurrentCenterId();
      });
      $scope.$watch('$ctrl.model.centerId', val => {
        if (val && $scope.vm && $scope.vm.config && $scope.vm.config.reportOwnerRegion)
          $scope.vm.config.reportOwnerRegion.ownerId = $scope.vm.config.reportOwnerRegion.regionId = undefined;
        $location.search('centerId', val).replace();
      });
    }

    /**
     * Map bankId to URL search param
     */
    function watchBankId() {
      $scope.$watch(() => $location.search().bankId, val => {
        $ctrl.model.bankId = val || val === 0 ? +val : $ctrl.banks && $ctrl.banks[0] && $ctrl.banks[0].id;
      });
      $scope.$watch('$ctrl.model.bankId', val => {
        $location.search('bankId', val).replace();
      });
    }

    /**
     * Map paperSize to URL search param
     */
    function watchPaperSize() {
      $scope.$watch(() => $location.search().paperSize, val => {
        $ctrl.model.paperSize = ($ctrl.optsPaperSize.find(o => o.id === val) || $ctrl.optsPaperSize.find(o => o.id === $ctrl.model.paperSize) || $ctrl.optsPaperSize[0]).id;
      });
      $scope.$watch('$ctrl.model.paperSize', val => {
        $location.search('paperSize', val).replace();
      });
    }

    /**
     * Map paperLayout to URL search param
     */
    function watchPaperLayout() {
      $scope.$watch(() => $location.search().paperLayout, val => {
        $ctrl.model.paperLayout = val || $ctrl.model.paperLayout || 'l';
      });
      $scope.$watch('$ctrl.model.paperLayout', val => {
        $location.search('paperLayout', val).replace();
      });
    }
  });
