import './pdf-preview.scss';

/**
 * PDF preview pane. Accepts PDF as Blob, creates/revokes temporary url and displays PDF in iframe.
 */
export default angular.module('pdfPreview', [
  'deskworks.helper',
])
  .directive('pdfPreview', function($window, helper) {

    return {
      restrict: 'A',
      scope: true,
      template:
        '<iframe ng-hide="ie" title="{{\'REPORTS.PREVIEW.HEADER\' | translate}}"></iframe>' +
        '<ff-info class="text-danger" ng-if="ie"><ff-icon sense="warning"></ff-icon> ' +
          'Microsoft Edge & Internet Explorer don\'t support live PDF preview. ' +
          'You can download the PDF or use another browser to have live preview, e.g. Chrome/Firefox/Opera/Safari.' +
        '</ff-info>',

      compile: function(tElement, tAttrs) {

        // TODO: Ugly hiding parent flex-form for mobile, can be refactored by wrapping flex-form to report-preview directive with ng-if
        const container = tElement.parent().parent();
        if (helper.isMobile() && container.hasClass('flex-form')) {
          tElement.parent().parent().css('display', 'none');
          return function link() {}; // browser doesn't support embedded PDF
        }

        if (helper.isIE()) {
          return function link($scope, $element, $attrs) {
            $scope.ie = true;
          };
        }

        tElement.addClass('pdf-preview');

        return function link($scope, $element, $attrs) {
          const iframe = $element.children();
          const dimmer = angular.element('<div class="ff-dimmer ff-spin"></div>');
          dimmer.on('click', function(event) { event.stopPropagation(); }); // block clicking

          /**
           * Stop Dimming
           */
          iframe.on('load', function() {
            angular.element(dimmer).remove();
            $element.removeClass('ff-dim');
          });

          // TODO: prevent vertical screen overflow: Math.min(maxHeight, clientHeight)

          /**
           * Watch for input blob and re-create temporary url for iframe
           */
          let pdfUrl; // temporary url created by URL.createObjectURL
          $scope.$watch($attrs.pdfPreview, (blob) => {
            // Start Dimming
            $element.append(dimmer);
            $element.addClass('ff-dim');

            // Clear previous temporary url
            if (pdfUrl) { $window.URL.revokeObjectURL(pdfUrl); pdfUrl = null; }

            if (blob) { // Create new temporary url and update iframe
              pdfUrl = $window.URL.createObjectURL(blob);
              iframe[0].src = pdfUrl;
            }
          });

          /**
           * Destroy temporary url for iframe
           */
          $scope.$on('$destroy', function() {
            if (pdfUrl) {
              $window.URL.revokeObjectURL(pdfUrl);
              pdfUrl = null;
            }
          });
        };
      }
    };
  });
